.set-component {

  .card {
    border: solid 1px #000;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
    box-sizing: border-box;
    float: left;
    margin: 5px 2%;
    padding: 20px;
    width: 42%;
  }

  .back {
    display: inline-block;
    width: 100%;
    word-wrap: break-word;
  }

  .front {
    margin: 5px 0;
  }

  .controls li {
    margin: 10px 0;
  }
}
